html {
  overflow-x: hidden;
  scrollbar-width: thin;
  scrollbar-color: #38bbb4 #dddddd;
}

html::-webkit-scrollbar {
  width: 8px;
}

html::-webkit-scrollbar-track {
  background-color: #DDD;
}

html::-webkit-scrollbar-thumb {
  background-color: #38BBB4;
}

body {
  font-family: 'reg';
  direction: ltr;
  text-align: left;
  overflow-y: hidden;
}

input:focus, a:focus, button:focus {
  outline: none;
  text-decoration: none;
}

input:hover, a:hover, button:hover {
  text-decoration: none;
}

button {
  cursor: pointer;
}

button:focus, button:active {
  outline: none;
  box-shadow: none;
}

ul.res {
  padding: 0;
  margin: 0;
  list-style: none;
}

@font-face {
  font-family: "reg";
  src: url("../fonts/OpenSans-Regular.ttf");
}

@font-face {
  font-family: "s-bold";
  src: url("../fonts/OpenSans-SemiBold.ttf");
}

@font-face {
  font-family: "bold";
  src: url("../fonts/OpenSans-Bold.ttf");
}

.loader-wrap {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  background-color: #3DDB9E;
  z-index: 9999;
}

.loader {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  height: 40px;
  width: 80px;
  box-sizing: border-box;
}

.loader .loader-item {
  position: relative;
  float: left;
  height: 40px;
  width: 4px;
  margin: 0 2px;
  background-color: #ffffff;
}

.loader .loader-item:nth-child(1) {
  animation: loader-item-1 2s linear infinite;
}

.loader .loader-item:nth-child(2) {
  animation: loader-item-2 2s linear infinite;
}

.loader .loader-item:nth-child(3) {
  animation: loader-item-3 2s linear infinite;
}

.loader .loader-item:nth-child(4) {
  animation: loader-item-4 2s linear infinite;
}

.loader .loader-item:nth-child(5) {
  animation: loader-item-5 2s linear infinite;
}

.loader .loader-item:nth-child(6) {
  animation: loader-item-6 2s linear infinite;
}

.loader .loader-item:nth-child(7) {
  animation: loader-item-7 2s linear infinite;
}

.loader .loader-item:nth-child(8) {
  animation: loader-item-8 2s linear infinite;
}

.loader .loader-item:nth-child(9) {
  animation: loader-item-9 2s linear infinite;
}

.loader .loader-item:nth-child(10) {
  animation: loader-item-10 2s linear infinite;
}

.loader:after {
  content: '... Loading';
  font-size: 16px;
  font-family: "Arial";
  color: #ffffff;
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  bottom: -32px;
  margin: auto;
}

@keyframes loader-item-1 {
  1% {
    transform: scaleY(1);
  }
  11% {
    transform: scaleY(1.4);
  }
  21% {
    transform: scaleY(1);
  }
  100% {
    transform: scaleY(1);
  }
}

