  color: #FFF;
}

.zx-header ul {
  display: flex;
  justify-content: flex-end;
  font-family: reg;
}

.zx-header .upper-nav ul {
  padding: 28px 0;
  margin: 0;
}

.zx-header .upper-nav ul li {
  padding: 0 10px;
  font-size: 12px;
}

.zx-header .upper-nav ul li a {
  color: #d2d9e2;
}

.zx-header .upper-nav ul li a span {
  padding: 0 8px;
}

.zx-header .upper-nav .lang {
  background-color: #FFF;
  width: 90px;
  height: 30px;
  color: #999;
  margin: 6px 0;
  padding: 6px 0;
  display: none;
  position: absolute;
}

.zx-header .upper-nav .lang p {
  margin: 0;
  text-align: center;
}

.zx-header .nav {
  display: flex;
  justify-content: space-between;
}

.zx-header .nav ul {
  padding: 0 0 28px 0;
  margin: 0;
  font-family: bold;
}

.zx-header .nav ul li {
  padding: 0 9px;
  font-size: 12px;
