}

.zx-header .nav ul li i {
  font-size: 11px;
  padding: 0 0 0 3px;
}

.zx-header .nav ul li .fa-search {
  cursor: pointer;
}

.zx-header .nav ul li:hover .stop {
  visibility: visible;
  opacity: 1;
}

.zx-header .nav .stop {
  visibility: hidden;
  opacity: 0;
  overflow: hidden;
  display: block !important;
  transition: all .5s ease-in-out;
}

.zx-header .nav .slide-menu-one {
  background-color: #DDD;
  position: absolute;
  top: 36px;
  width: max-content;
  display: block;
}

.zx-header .nav .slide-menu-one ul {
  padding: unset;
  flex-direction: column;
}

.zx-header .nav .slide-menu-one ul li {
  padding: 6px 12px;
}

.zx-header .nav .slide-menu-two {
  background-color: #DDD;
  position: absolute;
  top: 36px;
  width: max-content;
  display: none;
}

.zx-header .nav .slide-menu-two ul {
  padding: unset;
  flex-direction: column;
}

.zx-header .nav .slide-menu-two ul li {
  padding: 6px 12px;
}

.zx-header .nav .slide-menu-three {
  background-color: #DDD;
  position: absolute;
  top: 36px;
  width: max-content;
  display: none;
}

.zx-header .nav .slide-menu-three ul {
  padding: unset;
