.form-section .alert {
  margin: 45px 0 45px 0;
  border-radius: 0;
  font-family: reg;
  font-size: 13px;
  color: #0fa25b;
  background-color: #e8fff4;
  padding: 22px 20px;
  display: flex;
  align-items: center;
}

.form-section .alert i {
  padding: 0 12px 0 0;
  font-size: 26px;
}

.form-section .form h3 {
  color: #1a3a6a;
  font-family: bold;
  font-size: 16px;
}

.form-section .form h3::after {
  content: "";
  display: block;
  width: 30px;
  height: 2px;
  background-color: #eb3b3b;
  margin-top: 15px;
  transition: .5s ease;
}

.form-section .form span {
  font-family: reg;
  font-size: 13px;
  color: #eb3b3b;
  display: block;
  margin: 20px 0;
}

.form-section .form form .field label {
  font-family: s-bold;
  font-size: 14px;
  color: #555;
  padding: 13px 9px 6px;
}

.form-section .form form .field .required {
  color: #eb3b3b;
  display: unset;
  margin: unset;
}

.form-section .form form .field input {
  height: 50px;
  border-color: #e6e6e6;
  border-radius: 0;
}

.form-section .form form .field input:focus {
  outline: none;
  box-shadow: unset;
}

.form-section .form form .field textarea {
  height: 125px;
  border-color: #e6e6e6;
  border-radius: 0;
  resize: none;
}

.form-section .form form .field textarea:focus {
  outline: none;
  box-shadow: unset;
}

.form-section .form form .not-a-robot {
  border: 1px solid;
  border-color: #e6e6e6;
  background-color: #f9f9f9;
  width: 303px;
  height: 80px;
  margin: 12px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.form-section .form form .form-btn button {
  background-color: #eb3b3b;
  border-radius: 2px;
  width: 104px;
  height: 40px;
  border: none;
  color: #FFF;
  font-size: 13px;
  margin-top: 20px;
}

.form-section .info h3 {
  color: #1a3a6a;
  font-family: bold;
