@media (max-width: 992px) {
  .full-col {
    flex: 0 0 100%;
    max-width: 100%;
  }
  .slide-nav-menu {
    display: flex;
  }
  .upper-nav {
    display: none;
  }
  .zx-header .nav {
    padding: 16px 0;
  }
  .zx-header .nav ul {
    display: none;
  }
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 999;
  }
  .overlay .fixed-menu {
    width: 260px;
    height: 100%;
    position: absolute;
    top: 0;
    right: -260px;
    background-color: #143260;
  }
  .overlay .fixed-menu .fa-times {
    font-size: 16px;
    color: #FFF;
    padding: 12px 12px 0 12px;
  }
  .overlay .fixed-menu ul {
    padding: 6px 0;
  }
  .overlay .fixed-menu ul li {
    padding: 3px 12px;
    color: #FFF;
  }
  .overlay .fixed-menu ul li a {
    font-family: bold;
    font-size: 13px;
  }
  .overlay .fixed-menu ul li i {
    color: #FFF;
  }
  .overlay .fixed-menu ul hr {
    margin: 2px 0;
  }
  .Partnerships .back-color {
    margin: 10px 0;
  }
  .footer .container {
    flex-direction: column;
  }
  .footer .container .logo {
    padding: 40px 0 0 0;
  }
  .footer .container .logo .icons {
    margin: 0 0 30px 0;
  }
  .footer .container .logo .copy {
    margin: 0 0 15px 0;
  }
  .footer .container .site-map {
    padding: 30px 0 0 0;
  }
  .footer .container .site-map .active {
    background-color: #2359a9;
    padding: 4px 6px;
    border-radius: 2px;
  }
  .footer .container .services {
    padding: 30px 0 0 0;
  }
  .footer .container .services .active {
    background-color: #2359a9;
    padding: 4px 6px;
    border-radius: 2px;
  }
  .footer .container .contact {
    width: auto;
    padding: 60px 50px;
  }
}

@media (max-width: 360px) {
  .zx-header .zx-header-img h2 {
    text-align: center;
  }
  .footer .container .services {
    padding: 15px 0 0 0;
  }
}
