﻿html{
    &::-webkit-scrollbar {
        width: 8px;
    }
    &::-webkit-scrollbar-track {
        background-color: #DDD;
    }
    &::-webkit-scrollbar-thumb {
        // border-radius: 10px;
        background-color: #38BBB4;
    }

    overflow-x: hidden;

    scrollbar-width: thin;
    scrollbar-color: rgba(#38BBB4,1) rgba(#DDD,1);
}

body{
    font-family: 'reg';
    direction: ltr;
    text-align: left;
    overflow-y: hidden;
}

input, a, button{

    &:focus{
        outline: none;
        text-decoration: none;
    }

    &:hover{
        text-decoration: none;
    }
}

button{
    cursor: pointer;

    &:focus, &:active{
        outline: none;
        box-shadow: none;
    }
}

ul.res{
    padding: 0;
    margin: 0;
    list-style: none;
}


